.page-main {
  @media (max-width:$mobile-width-only) {
  }
}

.page-main__overlay {

  @media (max-width: $tablet-width) {

    &.page-main__overlay--opened {
      position: absolute;
      top:0;
      right:0;
      left:0;
      bottom:0;
      width:100%;
      height:100%;
      background-color: $main;
      z-index: 10;
    }
  }
}
